}
.page-inner {
  display: grid;
  grid-template-columns: minmax(20px, auto) repeat(2, minmax(auto, calc(960px / 2))) minmax(20px, auto);
  grid-template-rows: max-content minmax(29px, auto) max-content;
  padding-top: 11px;
}
.page-inner::before {
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("../img/bg-look-life-differently-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  background-color: #283645;
  z-index: -1;
  position: absolute;
  top: 0;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-inner::before {
    background-image: url("../img/bg-look-life-differently-mobile@2x.jpg");
  }
}
.page-inner--large {
  padding-bottom: 33px;
}
.page-inner__title {
  grid-column: 2/4;
  color: #FFFFFF;
}
@media (min-width: 660px) {
  .page-inner {
    padding-top: 30px;
  }
  .page-inner::before {
    background-image: url("../img/bg-look-life-differently-tablet.jpg");
  }
  .page-inner--large {
    padding-bottom: 68px;
  }
  .page-inner__title {
    font-size: 50px;
    font-weight: 300;
    line-height: 68px;
  }
}
@media (min-width: 660px) and (min-resolution: 144dpi), (min-width: 660px) and (min-resolution: 1.5dppx) {
  .page-inner::before {
    background-image: url("../img/bg-look-life-differently-tablet@2x.jpg");
  }
}
@media (min-width: 960px) {
  .page-header .page-inner {
    padding-top: 30px;
    padding-bottom: 26px;
  }
  .page-header .page-inner::before {
    background-image: url("../img/bg-look-life-differently-desktop.jpg");
  }
}
@media (min-width: 960px) and (min-resolution: 144dpi), (min-width: 960px) and (min-resolution: 1.5dppx) {
  .page-header .page-inner::before {
    background-image: url("../img/bg-look-life-differently-desktop@2x.jpg");
  }
}
.page-header {
  position: relative;
}
.page-header__wrap {
  background-color: #283645;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.page-header__container {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 17px;
  padding: 23px 20px 0 20px;
}
.page-header__logo {
  grid-column: 1/1;
}
.page-header__toggle {
  display: none;
  font-family: inherit;
  justify-self: flex-end;
  margin-top: -5px;
  grid-column: 2/-1;
  width: 50px;
  height: 30px;
  padding: 10px 10px 0 10px;
  background-color: transparent;
  border: none;
  position: relative;
  cursor: pointer;
}
.page-header__toggle::before,
.page-header__toggle::after {
  content: "";
  width: 30px;
  height: 2px;
  position: absolute;
  background-color: #FFFFFF;
  left: 24px;
  transform: rotate(45deg);
  top: 15px;
}
.page-header__toggle::before {
  transform: rotate(-45deg);
}
.page-header__toggle:hover::before,
.page-header__toggle:hover::after {
  background-color: #D22856;
}
.page-header__toggle:active::before,
.page-header__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.page-header__toggle.page-header--support-js {
  display: inline-block;
  vertical-align: middle;
}
.page-header__toggle--hamburger::before {
  transform: rotate(0deg);
  top: 3px;
  height: 3px;
  width: 48px;
  left: 0;
}
.page-header__toggle--hamburger::after {
  transform: rotate(0deg);
  height: 3px;
  width: 48px;
  left: 0;
  top: 13px;
  box-shadow: 0 9px 0 0 #FFFFFF;
}
.page-header__toggle--hamburger:hover::after {
  box-shadow: 0 9px 0 0 #D22856;
}
.page-header__toggle--hamburger:active::after {
  box-shadow: 0 9px 0 0 rgba(210, 40, 86, 0.3);
}
.page-header--closed .page-header__wrap {
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header--closed .page-header__nav {
  display: none;
}
.page-header--closed .page-header__bottom {
  position: static;
}
.page-header__nav {
  grid-column: 1 / -1;
  margin-left: -20px;
  margin-right: -20px;
  margin-top: 3px;
}
.page-header__bottom {
  position: relative;
}
@media (min-width: 660px) {
  .page-header__container {
    padding-top: 26px;
    grid-row-gap: 40px;
  }
  .page-header__toggle {
    margin-top: 14px;
  }
  .page-header__nav {
    margin-top: 0;
  }
}
@media (min-width: 960px) {
  .page-header__wrap {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__bottom {
    position: static;
    padding-top: 100px;
    padding-bottom: 85px;
  }
  .page-header__container {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto;
    grid-template-columns: 200px 1fr;
    grid-row-gap: 17px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 0;
  }
  .page-header__logo {
    margin-left: 0;
  }
  .page-header__toggle,
  .page-header__toggle.page-header--support-js {
    display: none;
  }
  .page-header--closed .page-header__nav {
    display: block;
  }
  .page-header__nav {
    grid-column: 2/-1;
    margin-top: 5px;
    margin-left: 0;
    margin-right: 0;
  }
}
.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-list__link {
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  padding: 15px 20px 16px 20px;
  display: block;
  box-sizing: border-box;
  border-top: 1px solid #000000;
  box-shadow: inset 0 1px rgba(255, 255, 255, 0.15);
}
.nav-list__link:hover,
.nav-list__link:focus {
  color: #D22856;
}
.nav-list__link:active {
  opacity: 0.3;
  color: #D22856;
}
.nav-list__item:last-child .nav-list__link {
  border-bottom: 1px solid #000000;
}
@media (min-width: 960px) {
  .nav-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    box-shadow: none;
  }
  .nav-list__link {
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
    border: none;
    box-shadow: none;
    padding: 15px 0;
    margin-right: 80px;
    margin-bottom: 10px;
  }
  .nav-list__link:active {
    color: #FFFFFF;
  }
  .nav-list__link--active {
    padding-bottom: 11px;
    box-shadow: 0 2px 0 0 #FFFFFF;
  }
  .nav-list__item:last-child .nav-list__link {
    border: none;
    margin-right: 0;
  }
}
